import { Card, Tag } from "antd";
import { FC } from "react";
import { IContainer } from "../types";

import "./index.scss";

export const Container: FC<IContainer> = ({
  number,
  request,
  location,
  last_comment,
  isLate,
  daysinway,
  client,
  click,
}) => {
  return (
    <Card
      size="small"
      type="inner"
      hoverable
      // style={{ width: 300, marginTop: 16 }}
      title={`${request} / ${number}`}
      onClick={click}
    >
      <div>
        <Tag color="processing">{location}</Tag>
        {isLate && <Tag color="error">опаздывает</Tag>}
      </div>
      <div className="details">
        {daysinway} дней в пути, {client}
      </div>
      <div className="comment_text">
        {last_comment.date} {last_comment.text}
      </div>
    </Card>
  );
};
