const FilterItem = ({
  text,
  name,
  number,
  filter,
  click,
}: {
  text: string;
  number: number;
  name: string;
  filter: string;
  click: () => void;
}) => {
  const clickHandler = () => {
    click();
  };
  return (
    <a
      onClick={clickHandler}
      style={{ color: name === filter ? "red" : "black" }}
      className='filter_item'
    >
      {text} {number}
    </a>
  );
};

export const ContainersPage: FC = () => {
  const [containers, setContainers] = useState<IContainer[]>(CONTAINERS);
  const [current, setCurrent] = useState<number | null>(null);
  const [filter, setFilter] = useState("");
  const [search, setSearch] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false);

  const today = new Date();

  const calculations: { [type: string]: IContainer[] } = {
    onBorder: CONTAINERS.filter(
      (container) => container.location === "На границе"
    ),
    onWay: CONTAINERS,
    waitGo: CONTAINERS.filter(
      (container) => container.location === "Ждёт отправления"
    ),
    isLate: CONTAINERS.filter((container) => container.isLate),
    noComment: CONTAINERS.filter((container) => container.last_comment.date),
    recentComment: CONTAINERS.filter((container) => !['14.09.2022','13.09.2022','12.09.2022',].includes(container.last_comment.date)),
  };

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const filterHandler = (filter: string) => {
    setFilter(filter);
    const filteredContainers = calculations[filter];
    setContainers(filteredContainers);
  };

  const clickHandler = (id: number) => {
      setCurrent(id);
    setIsModalOpen(true);
  };

  const searchHandler = (e: any) => {
    setSearch(e.target.value.toLowerCase());
  };

  const currentContainers = containers.filter(
    (container) =>
      container.number.toLowerCase().includes(search) ||
      container.request.toLowerCase().includes(search)
  );

  return (
    <>
      <div>
        <div className="filters">
          <FilterItem
            text="В пути"
            number={calculations.onWay.length}
            name="onWay"
            filter={filter}
            click={() => filterHandler("onWay")}
          />
          <FilterItem
            text="Ждут отправления"
            number={calculations.waitGo.length}
            name="waitGo"
            filter={filter}
            click={() => filterHandler("waitGo")}
          />
          <FilterItem
            text="На границе"
            number={calculations.onBorder.length}
            name="onBorder"
            filter={filter}
            click={() => filterHandler("onBorder")}
          />
          <FilterItem
            text="Опаздывает"
            number={calculations.isLate.length}
            name="isLate"
            filter={filter}
            click={() => filterHandler("isLate")}
          />
          <FilterItem
            text="Давно нет комментов"
            number={calculations.recentComment.length}
            name="recentComment"
            filter={filter}
            click={() => filterHandler("recentComment")}
          />
        </div>
        <div className="search">
          <Input
            size="large"
            placeholder="Найти по номеру контейнера или заявки"
            onChange={searchHandler}
          />
        </div>
        <div className="containers">
          {currentContainers.map((container) => (
            <Container {...container} click={() => clickHandler(container.id)}/>
          ))}
        </div>
      </div>
      <ContainerModal id={current} open={isModalOpen} onCancel={handleCancel}/>
    </>
  );
};
